.personal__textBox {
  width: 312px;
  height: 100%;
  display: flex;
  align-items: center;
}

.personal__textBox:last-child {
  margin-right: 250px;
}

.textBox__wrapper {
  margin-bottom: 0px;
  transform: perspective(10000px) rotateX(15deg) skewY(-15deg);
}

.textBox__isometricWrapper {
  width: 240px;
  height: max-content;
  padding-left: 20px;
}

.personal__isometricBackground {
  position: relative;
  width: 100%;
  height: 100vh;
  /* background-color: #181818; */
  border-radius: 20px;
  display: flex;
  justify-content: space-evenly;
}

.textBox__aside {
  width: 300px;
}

.personal h2 {
  position: relative;
  z-index: 1;
  font-size: 30px;
  font-style: normal;
  font-weight: normal;
  line-height: 52px;
  margin-left: 24px;
  letter-spacing: 0.03em;
  transform: skewX(45deg);
}

.isometric__frontPlane {
  transform: skewX(0deg) !important;
  margin-left: -80px !important;
}

.isometricHeading {
  position: relative;
  width: 300px;
  margin-bottom: 40px;
}

.isometricHeading__decorator {
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: linear-gradient(90deg, #9a1aff 0%, #e864cb 100%);
  transform: skewX(45deg);
}

.isometricHeading__decorator::before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 20px;
  transform: skewY(-45deg) translate(-20px, -10px);
  background: #9a1aff;
  filter: brightness(0.5);
}

.isometricHeading__decorator::after {
  content: '';
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  height: 20px;
  width: 100%;
  background: linear-gradient(90deg, #9a1aff 0%, #e864cb 100%);
  filter: brightness(0.5);
  transform: skewX(-45deg) translate(10px, 20px);
}

