.isometric__skillList {
  width: 250px;
}

.isometric__skillBox {
  margin-top: -10px;
  margin-bottom: 20px;
  position: relative;
  z-index: 1;
  width: 100%;
  margin-left: 20px;
  height: 52px;
  background: linear-gradient(90deg, #9a1aff 0%, #e864cb 100%);
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate(0, 0);
  transition: 0.3s;
}

.isometric__skillBox::before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 20px;
  background: #9a1aff;
  filter: brightness(0.5);
  transform: skewY(45deg) translate(-20px, 10px);
}

.isometric__skillBox::after {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 20px;
  width: 100%;
  background: linear-gradient(90deg, #9a1aff 0%, #e864cb 100%);
  filter: brightness(0.5);
  transform: skewX(45deg) translate(10px, -20px);
}

.isometric__skill {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* cursor: pointer; */
  cursor: url('../../cursors/cursor_pointer.svg'), auto;
}

.isometric__skillRatingWrapper {
  width: 150px;
  display: flex;
  justify-content: space-between;
}

.isometric__skillRating {
  position: relative;
  z-index: 1;
  width: 20px;
  height: 20px;
  background: #fff;
  font-size: 40px;
  transform: translate(0, 0);
  /* box-shadow: -7px -7px 10px rgba(255, 255, 255, 0); */
  transition: 0.1s;
  /* transition: box-shadow .5s; */
}

.isometric__skillRating:hover {
  transform: translate(0, -2px);
  transition: 0.1s ease-out;
}

.isometric__skillRating::before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 5px;
  background: grey;
  transform: skewY(45deg) translate(-5px, 2.5px);
}

.isometric__skillRating::after {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 5px;
  width: 100%;
  background: grey;
  transform: skewX(45deg) translate(2.5px, -5px);
}

.isometric__skillRating_active {
  background: #f0db4f;
}

.isometric__skillRating_active::before,
.isometric__skillRating_active::after {
  background: #f0db4f;
  filter: brightness(0.5);
}

.isometric__skillAnim {
  position: absolute;
  top: 18px;
  font-size: 18px;
  /* font-weight: bold; */
  opacity: 0;
  transition: 0.5s;
  height: 100%;
  line-height: 100%;
}

.isometric__skill:hover .isometric__skillAnim {
  opacity: 1;
  transition: 0.5s;
}

