.cursorWrapper {
  width: 200px;
  height: 200px;
  border-radius: 50px;
  position: absolute;
  top: -200px;
  left: -200px;
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(250, 222, 43, 0.336) 0%,
    rgba(250, 222, 43, 0) 80%
  );
  -webkit-filter: blur(33px);
          filter: blur(33px);
  pointer-events: none;
  z-index: 100;
  -webkit-animation: scale 5s linear infinite alternate;
          animation: scale 5s linear infinite alternate;
}

@-webkit-keyframes scale {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  to {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

