  cursor: url(/e-bubble/static/media/cursor_pointer.404a1040.svg), auto;
}

.lift__floor_active .lift__box {
  background: #f0db4f;
  transition: 0.3s;
}

.lift__floor_active .lift__box::before {
  background: #f0db4f;
  transition: 0.3s;
}

.lift__floor_active .lift__box::after {
  background: #f0db4f;
  transition: 0.3s;
}

.lift__floor_active .lift__name {
  color: #f0db4f;
  transition: 0.3s;
}

.lift__floor_active .lift__gate {
  width: 0;
  background: #181818;
  transition: 0.5s;
}

.lift__box {
  position: relative;
  width: 20px;
  height: 15px;
  background: white;
  transition: 0.3s;
}

.lift__box::before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 10px;
  background: rgba(255, 255, 255, 0.5);
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5);
  -webkit-transform: skewY(-45deg) translate(-10px, -5px);
          transform: skewY(-45deg) translate(-10px, -5px);
}

.lift__box::after {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 10px;
  width: 100%;
  background: rgba(255, 255, 255, 0.5);
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5);
  -webkit-transform: skewX(-45deg) translate(10px, 15px);
          transform: skewX(-45deg) translate(10px, 15px);
}

.lift__name {
  position: relative;
  -webkit-transform: skewX(-45deg) translate(0px, 7px);
          transform: skewX(-45deg) translate(0px, 7px);
  font-size: 24px;
  height: 36px;
  transition: 0.3s;
}

.lift__gate {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: #181818;
  transition: 0.5s;
}

.navigation__headingAnimation {
